webpackJsonp([207993318285012],{

/***/ 359:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<p>El módulo de Sensores de Línea consiste en un arreglo de 5 sensores ópticos reflectivos (<strong>QRE1113</strong>), los cuales funcionan como entradas digitales. Este módulo se comunica con el <strong>Shield</strong> usando el puerto <strong>IIC</strong>, por lo que no es necesario especificar el puerto al cual se conecta.</p>\n<p>El objetivo de este módulo es proporcionar el mecanismo necesario para detectar trayectorias o pistas sobre una plataforma. Con ello se tiene la posibilidad de construir robots <strong>Seguidores de Línea</strong>, que resuelven <strong>laberintos</strong>, o que buscan algún patrón en una plataforma. Con esto se proporcionan elementos fundamentales para aprendizaje de electrónica y programación, ya que en el diseño de este tipo de robots normalmente se implementan algoritmos para búsqueda.</p>\n<h2>Instalación de la librería</h2>\n<p>La instalación de la librería se puede realizar usando el gestor de paquetes <a href=\"https://www.npmjs.com/package/robotois-line-sensor\"><strong>npm</strong></a> o descargando la última versión disponible en el repositorio de <a href=\"https://github.com/Robotois/robotois-line-sensors\"><strong>GitHub</strong></a>:</p>\n<ul>\n<li>Usando <em>npm</em> se debe ejecutar el siguiente comando:</li>\n</ul>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-bash\"><code><span class=\"token function\">npm</span> <span class=\"token function\">install</span> robotois-line-sensor\n</code></pre>\n      </div>\n<ul>\n<li>Para descargar la última versión en <em>GitHub</em> se ejecuta la siguiente línea en el <em>shell</em>:</li>\n</ul>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-bash\"><code><span class=\"token function\">git</span> clone https://github.com/Robotois/robotois-line-sensors.git\n</code></pre>\n      </div>\n<p>Para este caso es necesario instalar las dependencias de la librería, lo cual se realiza con lo siguiente (en la ruta del repositorio): <code>npm install</code>.</p>\n<!-- # Conexión al Shield -->\n<h2>Configurar la plataforma</h2>\n<p>El primer paso es definir la plataforma sobre la cual se moverá el robot, para lo cual se han considerado dos posibles escenarios: la plataforma es blanca y la trayecoria es negra, o una plataforma negra y la trayectoria blanca.</p>\n<p>Para configurar el módulo se usa la función <code>setBackground()</code>, a la cual se le pasa como parámetro el texto con el fondo de la plataforma:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-javascript\"><code><span class=\"token keyword\">const</span> LineSensors <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'robotois-line-sensors'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">const</span> lineSensors <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">LineSensors</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\nlineSensors<span class=\"token punctuation\">.</span><span class=\"token function\">setBackground</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"white\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>ó:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-javascript\"><code><span class=\"token keyword\">const</span> LineSensors <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'robotois-line-sensors'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">const</span> lineSensors <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">LineSensors</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\nlineSensors<span class=\"token punctuation\">.</span><span class=\"token function\">setBackground</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"black\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>No obstante, el valor por default del color de fondo de la plataforma es <code>white</code>, así que solo será necesario usar esta función cuando el color de fondo sea negro.</p>\n<h2>Mediciones</h2>\n<p>Para obtener mediciones de manera sencilla, en esta librería se cuenta con mediciones por eventos, es decir que se cada vez que se tenga una nueva medición se dispara un evento con las mediciones actuales.</p>\n<p>Para comprender el funcionamiento de este módulo, se considerará que las mediciones se realizan sobre una plataforma blanca y una trayectoria de color negro.</p>\n<p>Para utilizar las mediciones por eventos, éstos se deben habilitar con la función <code>enableEvents()</code>. En el siguiente ejemplo se muestra cómo utilizar los eventos de mediciones:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-javascript\"><code><span class=\"token keyword\">const</span> LineSensors <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'robotois-line-sensors'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">const</span> lineSensors <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">LineSensors</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\nlineSensors<span class=\"token punctuation\">.</span><span class=\"token function\">enableEvents</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\nlineSensors<span class=\"token punctuation\">.</span><span class=\"token function\">on</span><span class=\"token punctuation\">(</span><span class=\"token string\">'medicion'</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">(</span>_line<span class=\"token punctuation\">,</span> _sensors<span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n  console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span><span class=\"token template-string\"><span class=\"token string\">`Linea: </span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>_line<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">, Sensores: </span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>_sensors<span class=\"token punctuation\">.</span><span class=\"token function\">toString</span><span class=\"token punctuation\">(</span><span class=\"token number\">16</span><span class=\"token punctuation\">)</span><span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">`</span></span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>El resultado del ejemplo anterior es similar al siguiente:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-text\"><code>Linea: 0, Sensores: 1\nLinea: 50, Sensores: 3\nLinea: 100, Sensores: 2\nLinea: 100, Sensores: 2\nLinea: 200, Sensores: 4\nLinea: 200, Sensores: 4\nLinea: 200, Sensores: 4\nLinea: 300, Sensores: 8\nLinea: 300, Sensores: 8\nLinea: 300, Sensores: 8\nLinea: 350, Sensores: 18\nLinea: 400, Sensores: 10\nLinea: 400, Sensores: 10</code></pre>\n      </div>\n<p>Como se puede observar, el estado de los sensores se obtienen a través de dos tipos de valores <code>line</code> y <code>sensors</code>. El valor de línea (<code>line</code>), considera un rango de <code>0-400</code> en donde los valores mas cercanos a <code>0</code> indican que la trayectoria se encuentra del lado izquierdo y los valores cercanos a <code>400</code> indican que la trayectoria está a la derecha. Consecuentemente, el valor <code>200</code> implica que la trayectoria se encuentra justo en el centro del módulo.</p>\n<p>El valor medido, que en el ejemplo se llama <code>sensors</code>, proporciona el valor hexadecimal equivalente a el estado actual de los sensores, en donde se considera que los sensores son un arreglo binario y cuyo estado puede ser <code>0-1</code>. En este caso el estado de los sensores se representan a través de <strong>5 bits</strong>. Para obtener una representación más intuitiva, se cambiará el código que imprime en consola los valores de la medición:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-javascript\"><code><span class=\"token keyword\">const</span> LineSensors <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'robotois-line-sensors'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">const</span> lineSensors <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">LineSensors</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\nlineSensors<span class=\"token punctuation\">.</span><span class=\"token function\">enableEvents</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\nlineSensors<span class=\"token punctuation\">.</span><span class=\"token function\">on</span><span class=\"token punctuation\">(</span><span class=\"token string\">'medicion'</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">(</span>_line<span class=\"token punctuation\">,</span> _sensors<span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n  console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span><span class=\"token template-string\"><span class=\"token string\">`Linea: </span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>_line<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">, Sensores: </span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>lineSensors<span class=\"token punctuation\">.</span><span class=\"token function\">sensorsToString</span><span class=\"token punctuation\">(</span>_sensors<span class=\"token punctuation\">)</span><span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">`</span></span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-text\"><code>Linea: 0, Sensores: 00001\nLinea: 100, Sensores: 00010\nLinea: 100, Sensores: 00010\nLinea: 150, Sensores: 00110\nLinea: 200, Sensores: 00100\nLinea: 200, Sensores: 00100\nLinea: 200, Sensores: 00100\nLinea: 300, Sensores: 01000\nLinea: 300, Sensores: 01000\nLinea: 300, Sensores: 01000\nLinea: 350, Sensores: 11000\nLinea: 350, Sensores: 11000\nLinea: 400, Sensores: 10000\nLinea: 400, Sensores: 10000</code></pre>\n      </div>\n<p>Por todo lo anterior, este método para obtener mediciones de los sensores es muy útil para diversas aplicaciones, ya que es posible implementar sistemas de control tipo <strong>PI</strong> o <strong>PID</strong> que son muy comunes en robots de competencias, es posible implementar algoritmos para realizar búsquedas específicas como la resolución de laberintos, solo por mencionar algunos casos.</p>\n<h1>Mediciones Específicas</h1>\n<p>En la sección anterior se mostró la manera de tomar mediciones de los sensores usando eventos, lo cual puede ser útil como base para proyectos sencillos. Sin embargo, para ciertas aplicaciones se requiere realizar mediciones en determinados escenarios. Por lo que en esta librería se proporcionan las funciones de medición de manera individual.</p>\n<h2>Función <code>readSensors()</code></h2>\n<p>Esta función obtiene el estado actual de los sensores en forma digital, el cual se representa en un valor numérico entero. El valor numérico se forma considerando los sensores como un arreglo binario cuyo estado puede ser <code>0-1</code>. De hecho, esta función es utilizada cuando se obtienen mediciones por eventos, en donde se considera un arreglo con <strong>5 bits</strong>. Por ello el siguiente ejemplo arroja valores similares al ejemplo mostrado en la sección anterior:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-javascript\"><code><span class=\"token keyword\">const</span> LineSensors <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'robotois-line-sensors'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">const</span> lineSensors <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">LineSensors</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token function\">setInterval</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n  console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span><span class=\"token template-string\"><span class=\"token string\">`Sensores: </span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>lineSensors<span class=\"token punctuation\">.</span><span class=\"token function\">sensorsToString</span><span class=\"token punctuation\">(</span>lineSensors<span class=\"token punctuation\">.</span><span class=\"token function\">readSensors</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">`</span></span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span><span class=\"token number\">500</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>El resultado de ejecutar este ejemplo muestra el valor de los sensores en forma binaria, en donde el valor <code>1</code> indica que ese sensor ha detectado la trayectoria o simplemente esta activo, y el valor <code>0</code> indica que ese sensor no esta detectando nada. El siguiente resultado se obtuvo al mover el módulo de sensores de izquierda a derecha y viceversa sobre una línea negra en una plataforma de color blanco:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-text\"><code>Sensores: 10000\nSensores: 01000\nSensores: 01100\nSensores: 00100\nSensores: 00010\nSensores: 00001\nSensores: 00001\nSensores: 00010\nSensores: 00010\nSensores: 00100\nSensores: 00100\nSensores: 01000\nSensores: 01000\nSensores: 11000\nSensores: 10000</code></pre>\n      </div>\n<h2>Función <code>readLine()</code></h2>\n<p>Esta función permite obtener el estado de los sensores, pero en este caso el valor numérico que devuelve tiene un rango de <code>0-400</code>, en donde los valores cercanos a <code>0</code> indican que la trayectoria o línea se encuentra a la izquierda del módulo y los valores cercanos a <code>400</code> indican que la trayectoria está a la derecha del módulo. Cuando se tiene el valor <code>200</code> implica que la trayectoria se encuentra en el centro del módulo.</p>\n<p>Normalmente, en aplicaciones donde se tiene que seguir una trayectoria, el objetivo principal es que el módulo siempre se mantenga centrado sobre la trayectoria para no perder la ruta. Un ejemplo de cómo utilizar esta función es el siguiente:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-javascript\"><code><span class=\"token keyword\">const</span> LineSensors <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'robotois-line-sensors'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">const</span> lineSensors <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">LineSensors</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token function\">setInterval</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n  console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span><span class=\"token template-string\"><span class=\"token string\">`Sensores: </span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>lineSensors<span class=\"token punctuation\">.</span><span class=\"token function\">readLine</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">`</span></span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span><span class=\"token number\">500</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>En el resultado obtenido, nuevamente se movió el módulo de izquierda a derecha y viceversa, con el objetivo de mostrar cómo cambia el valor que devuelve la función <code>readLine()</code>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-text\"><code>Sensores: 0\nSensores: 100\nSensores: 200\nSensores: 300\nSensores: 300\nSensores: 400\nSensores: 400\nSensores: 400\nSensores: 400\nSensores: 300\nSensores: 300\nSensores: 250\nSensores: 200\nSensores: 150\nSensores: 100\nSensores: 100\nSensores: 100\nSensores: 50\nSensores: 0</code></pre>\n      </div>\n<p>Este resultado es muy útil en el sentido que debido a que se tiene un valor numérico acotado en un rango, es posible implementar algoritmos de control de manera fácil, por ejemplo para que el robot siga una trayectoria.</p>\n<h2>Función <code>readSensor()</code></h2>\n<p>Esta función obtiene el estado actual de un sensor en particular, el valor obtenido del estado del sensor, que puede ser <code>0-1</code> que indica si se está detectando la trayectoria o no. Un ejemplo simple sería el siguiente:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-javascript\"><code><span class=\"token keyword\">const</span> LineSensors <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'robotois-line-sensors'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">const</span> lineSensors <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">LineSensors</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token function\">setInterval</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n  console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span><span class=\"token template-string\"><span class=\"token string\">`Sensor #1: </span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>lineSensors<span class=\"token punctuation\">.</span><span class=\"token function\">readSensor</span><span class=\"token punctuation\">(</span><span class=\"token number\">1</span><span class=\"token punctuation\">)</span><span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">`</span></span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span> <span class=\"token number\">1000</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>En este ejemplo el resultado es similar al siguiente:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-text\"><code>Sensor #1: 0\nSensor #1: 0\nSensor #1: 1\nSensor #1: 1\nSensor #1: 1\nSensor #1: 0\nSensor #1: 0\nSensor #1: 0</code></pre>\n      </div>\n<p>Como se observa, esta función es muy útil para evaluar el estado de un sensor en particular, sin embargo no se recomienda usarlo para evaluar el estado de varios sensores al mismo tiempo por razones de rendimiento y redundancia. Esto debido a que internamente la función <code>readSensor()</code> obtiene el estado de todos los sensores y posteriormente evalúa el estado del sensor que se ha especificado. Por lo que, para evaluar el estado de varios sensores, se recomienda usar operaciones a nivel de bits en combinación con la función <strong><code>readSensors()</code></strong>.</p>\n<p>Una manera sencilla de resolver este problema es la siguiente:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"gatsby-code-javascript\"><code><span class=\"token keyword\">const</span> LineSensors <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'robotois-line-sensors'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">const</span> lineSensors <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">LineSensors</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">function</span> <span class=\"token function\">sensorStatus</span><span class=\"token punctuation\">(</span>i<span class=\"token punctuation\">,</span> sensors<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">return</span> <span class=\"token punctuation\">(</span>sensors <span class=\"token operator\">>></span> <span class=\"token punctuation\">(</span>i <span class=\"token operator\">-</span> <span class=\"token number\">1</span> <span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">&amp;</span> <span class=\"token number\">0x01</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token function\">setInterval</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">const</span> sensors <span class=\"token operator\">=</span> lineSensors<span class=\"token punctuation\">.</span><span class=\"token function\">readSensors</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span><span class=\"token template-string\"><span class=\"token string\">`Sensor #1: </span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span><span class=\"token function\">sensorStatus</span><span class=\"token punctuation\">(</span><span class=\"token number\">1</span><span class=\"token punctuation\">,</span> sensors<span class=\"token punctuation\">)</span><span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">`</span></span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span> <span class=\"token number\">1000</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>","frontmatter":{"title":"Sensores de Linea","path":"/docs/line-sensors.html"}}},"pathContext":{}}

/***/ })

});


